//  request 
import request from '../request'
// 
export const logList = (params) => {
    return request({
        url: "/recharge/record/list",
        method: 'post',
        data: params
    })
}

// 
export const refundRechargeRecord = (params) => {
    return request({
        url: "/recharge/record/refund",
        method: 'post',
        data: params
    })
}
