//  request 
import request from '../request'
import app from '../app.js';
let {appid } = app;

// openid
export const getOpenId = (params) => {
    return request({
        url: `/wx/ma/${appid}/login`,
        method: 'get',
        data: params
    })
}
// 
export const getPhone = (params) => {
    return request({
        url: `/wx/ma/${appid}/phone`,
        method: 'get',
        data: params
    })
}
// 
export const getInfo = (params) => {
    return request({
        url: `/wx/ma/${appid}/info`,
        method: 'get',
        data: params
    })
}

