//  request 
import request from '../request'
// 
export const saveOrder = (params) => {
    return request({
        url: "/charging/order/saveOrder",
        method: 'post',
        data: params
    })
}
// 
export const single = (params) => {
    return request({
        url: "/charging/order/single",
        method: 'post',
        data: params
    })
}
