//  request 
import request from '../request'


// 
export const getUserInfo = (params) => {
    return request({
        url: `/user/info`,
        method: 'post',
        data: params
    })
}

// 
export const updateUserInfo = (params) => {
    return request({
        url: `/user/update`,
        method: 'post',
        data: params
    })
}

