//  request 
import request from "../request";

// 
export const getHeadListAll = (params) => {
  return request({
    url: `/user/invoice/header/all`,
    method: "post",
    data: params,
  });
};

// 
export const getHeadList = (params) => {
  return request({
    url: `/user/invoice/header/list`,
    method: "post",
    data: params,
  });
};

