//  request 
import request from '../request'

// 
export const cardList = (params) => {
    return request({
        url: "/card/list",
        method: 'post',
        data: params
    })
}
// 
export const bindCard = (params) => {
    return request({
        url: "/card/bindCard",
        method: 'post',
        data: params
    })
}